.header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  object-fit: cover;
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.header__logo-link {
  object-fit: cover;
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  text-transform: uppercase;
  margin-right: 50px;
}

.nav__list {
  flex-flow: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__list > li {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 20px;
  display: flex;
}

.nav__link {
  color: #060b35;
  justify-content: center;
  align-items: center;
  padding: 23px 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  transition: all .4s;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

body {
  margin: 0;
  padding: 0;
}

h3, h4 {
  margin: 0;
}

.container {
  grid-template-columns: repeat(auto-fit, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  transition: all .4s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars {
  justify-content: space-between;
  justify-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  justify-content: center;
  align-items: end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__product-price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .4s;
  display: flex;
}

.card__button:hover {
  box-sizing: border-box;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  justify-content: space-between;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
